html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #F6F6F6;
  color: #000;
}

p {
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-family: "Indie Flower", cursive;
  color: #FFFFFF;
}

img {
  max-width: 100%;
}

.btn {
  padding: 1rem 5.6rem;
  background-color: #A267AC;
  color: #000;
  font-size: 2rem;
  border-radius: 50px;
  border: none;
  display: block;
  margin: 4rem auto;
}
.btn:hover {
  cursor: pointer;
  background-color: #B762C1;
}

.login__container {
  background-color: #000;
  color: #FFFFFF;
  display: flex;
  flex-direction: column-reverse;
}

.login {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0.8333333333rem;
  align-items: center;
}

.logo__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero {
  background-image: url("../img/hero.jpg");
  background-size: cover;
  background-position: center center;
  min-height: 150px;
  margin-top: 2rem;
}

.logo {
  margin: 2rem auto 0 auto;
  height: 15rem;
}

.links__container {
  display: flex;
  flex-direction: column;
  padding-left: 4rem;
  gap: 2rem;
  font-size: 1.7rem;
  text-align: left;
}
.links__container a {
  margin: 0 auto;
  color: #9AD0EC;
  transition: all 0.2s ease-in-out;
}
.links__container a:hover {
  color: #398AB9;
}

.btn__container {
  display: inline-block;
  width: 100%;
}

@media (min-width: 768px) {
  .login__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    min-height: 100vh;
  }

  .logo {
    margin: 0 auto;
    height: 25rem;
  }

  .hero {
    margin: 0;
  }

  .links__container {
    flex-direction: row;
    justify-content: space-between;
    font-size: 2rem;
  }
}
.campo {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.campo label {
  margin-right: 2rem;
}
.campo input {
  width: 95%;
  padding: 1rem;
  border-radius: 11px;
}